@media only screen and (max-width: 600px) and (min-width: 240px) {
  *{
    box-sizing: border-box;
  }

  .App{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    color: white;
    height: 100vh;
    background-color: black;
    scroll-behavior: smooth;
  }

  .background{
    position: relative;
    width: 100%;
    height: 100%;
    background-image: url(../src/images/homebg.jpeg);
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
    filter: brightness(50%);
  }

  .logo{
    border-radius: 0px 10px 0px 10px;
    width: 162px;
    background-image: linear-gradient(to right, rgba(255, 255, 0, 0.200), rgba(255, 255, 0, 0.350), rgba(255, 255, 0, 0.450));
    color: black;
    padding: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 2rem;
    display: flex;
    flex-direction: column;
    line-height: 24px;
    margin: 0;
  }

  .logo-caption{
    font-size: 0.9rem;
    letter-spacing: 4px;
  }

  .address{
    width: 100%;
    position: absolute;
    bottom: 10px;
    text-align: center;
    z-index: 0;
  }

  .buttons{
    position: absolute;
    top: 40px;
    left: 5%;
  }

  .menu, .contact, .sendmsg{
    background-color: transparent;
    border: 1.5px solid rgba(255, 255, 0, 0.459);
    color: white;
    padding: 8px 20px;
    outline: none;
  }

  .menu:active, .contact:active{
    outline:none;
  }

  .contact{
    margin-top: 20px;
  }

  .workdays{
    position: absolute;
    bottom: 100px;
    line-height: 8px;
    left: 5%;
    z-index: 0;
  }

  .displaymenu{
    position: absolute;
    top: 30px;
    left: 0;
    right: 0;
    margin: 0 auto;
    height: 94%;
    max-height: 100%;
    width: 90%;
    background-color: black;
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
    display: none;
  }

  .displaymenutext{
    position: absolute;
    color: white;
    top: 30px;
    left: 0;
    right: 0;
    margin: 0 auto;
    height: 94%;
    width: 90%;
    overflow: scroll;
    scrollbar-width: none;
    display: none;
  }

  .displaymenutext>p{
    font-size: 0.9rem;
    color: whitesmoke;
    padding: 5px;
    
  }

  .foods, .info{
    border-radius: 0px 0px 10px 10px;
    font-weight: 500;
    color: black;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding: 10px;
    font-size: 1.6rem;
    background-image: linear-gradient(to right, rgba(255, 255, 0, 0.200), rgba(255, 255, 0, 0.350), rgba(255, 255, 0, 0.450));
  }

  .food-details, .contact-details{
    padding: 0px 10px; 
  }

  #menuform{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding-bottom: 10px;
    padding-right: 10px;
  }

  textarea{
    width: 100%;
    font-size: 1.1rem;
    padding: 10px;
  }

  .cf{
    width: 100%;
    padding: 10px;
    margin-bottom: 4px;
  }

  .circle{
    border: 2px solid black;
    background-color: white;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    position: absolute;
    top: 5px;
    right: 5px;
    color: black;
    text-align: center;
    font-size: 0.8rem;
  }

  .fa-shopping-cart{
    color: rgba(255, 255, 0, 0.459);
    position: absolute;
    top: 60%;
    left: 20px;
    transform: translate(-50%, -50%);
    font-size: 1.5rem;
  }

  .cartblock{
    position: absolute;
    top: 40px;
    right: 30px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 1.5px solid rgba(255, 255, 0, 0.459);
  }

  .menublock, .contactblock{
    display: none;
  }

  .menublock{
    width: 90%;
    margin: 40px auto;
    transition: 1s;
    border-left: 1px solid rgba(255, 255, 0, 0.200);
    border-right: 1px solid rgba(255, 255, 0, 0.350);
    border-bottom: 1px solid rgba(255, 255, 0, 0.450);
  }

  .contactblock{
    width: 90%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 1s;
  }

  input:focus, textarea:focus{
    outline: none;
  }

  .close{
    position: sticky;
    top: 10px;
    font-weight: bolder;
    float: right;
  }
}
